    box-shadow: none;\n    .chat-header {\n      display: none;\n    }\n    .chatbox {\n      .chat-footer {\n        border-top: none;\n        padding: 0 10px 10px;\n      }\n    }\n  }\n  h1 {\n    font-size: 3.2em;\n    line-height: 1.1;\n  }\n  a {\n    font-weight: 500;\n    color: #646cff;\n    text-decoration: inherit;\n  }\n  a:hover {\n    color: #535bf2;\n  }\n\n  position: fixed;\n  bottom: 6rem;\n  right: 3rem;\n  width: 450px;\n  max-width: calc(100% - 3rem);\n  height: 85vh;\n  display: flex;\n  flex-direction: column;\n  box-shadow: rgba(150, 150, 150, 0.2) 0px 10px 30px 0px,\n    rgba(150, 150, 150, 0.2) 0px 0px 0px 1px;\n  border-radius: 1rem;\n  overflow: hidden;\n  background: #fff;\n  @media screen and (max-width: 540px) {\n    right: 1rem;\n    max-width: calc(100% - 2rem);\n  }\n  .chatbox {\n    display: flex;\n    flex-direction: column;\n    height: 100%;\n    .chat-header {\n      flex: 0 0 auto;\n      border-bottom: 1px solid #ddd;\n      .chat-brand {\n        display: flex;\n        justify-content: space-between;\n        padding: 1rem;\n        .chatbox-logo {\n          width: auto;\n          object-fit: contain;\n          height: 36px;\n        }\n        .btn-wrapper {\n          display: flex;\n          justify-content: space-between;\n          gap: 0.5rem;\n          .close-icon {\n            display: flex;\n            align-items: center;\n            border: none;\n            outline: none;\n            background: transparent;\n            cursor: pointer;\n            img {\n              width: 16px;\n              cursor: pointer;\n            }\n          }\n        }\n      }\n    }\n    .chat-body {\n      flex: 1 1 auto;\n      overflow-y: auto;\n      padding: 2rem 1rem;\n      // scrollbar-gutter: stable;\n      position: relative;\n\n      .chat-message {\n        display: flex;\n        flex-direction: column;\n        align-items: flex-start;\n        margin-bottom: 1rem;\n\n        .attachments-container {\n          display: flex;\n          flex-wrap: wrap;\n          gap: 8px;\n          margin-top: 8px;\n          max-width: 80%;\n          justify-content: flex-end;\n\n          .attachment-item {\n            width: 120px;\n            height: 120px;\n            border-radius: 8px;\n            overflow: hidden;\n            background: #f5f5f5;\n            border: 1px solid #ddd;\n\n            .image-preview {\n              width: 100%;\n              height: 100%;\n\n              img {\n                width: 100%;\n                height: 100%;\n                object-fit: cover;\n                margin: 0;\n              }\n            }\n\n            .file-preview {\n              width: 100%;\n              height: 100%;\n              display: flex;\n              align-items: center;\n              justify-content: center;\n              background: #f5f5f5;\n\n              .file-icon {\n                width: 40px;\n                height: 40px;\n                border-radius: 4px;\n                background: #fff;\n                display: flex;\n                align-items: center;\n                justify-content: center;\n                border: 1px solid #ddd;\n\n                .file-extension {\n                  font-size: 12px;\n                  font-weight: 500;\n                  color: #666;\n                  text-transform: uppercase;\n                }\n              }\n            }\n          }\n        }\n\n        .chat-content {\n          padding: 0.5rem 1rem;\n          border-radius: 0.5rem;\n          background-color: #f1f1f0;\n          max-width: 80%;\n          word-break: break-word;\n          .timestamp {\n            font-size: 0.75rem;\n            color: #999;\n            margin-top: 0.25rem;\n          }\n\n          .message-attachments {\n            margin-top: 8px;\n            display: flex;\n            flex-direction: column;\n            gap: 8px;\n\n            .attachment-image {\n              display: flex;\n              flex-direction: column;\n              gap: 4px;\n              max-width: 200px;\n\n              img {\n                width: 100%;\n                height: auto;\n                border-radius: 8px;\n                object-fit: cover;\n              }\n\n              .attachment-name {\n                font-size: 12px;\n                color: rgba(255, 255, 255, 0.8);\n                overflow: hidden;\n                text-overflow: ellipsis;\n                white-space: nowrap;\n              }\n            }\n\n            .attachment-file {\n              a {\n                display: flex;\n                align-items: center;\n                gap: 8px;\n                padding: 8px;\n                background: rgba(0, 0, 0, 0.1);\n                border-radius: 8px;\n                color: inherit;\n                text-decoration: none;\n                transition: background-color 0.2s;\n                max-width: 200px;\n\n                &:hover {\n                  background: rgba(0, 0, 0, 0.2);\n                }\n\n                .file-preview {\n                  width: 40px;\n                  height: 40px;\n                  border-radius: 4px;\n                  overflow: hidden;\n                  display: flex;\n                  align-items: center;\n                  justify-content: center;\n                  background: rgba(255, 255, 255, 0.1);\n                  flex-shrink: 0;\n\n                  .file-extension {\n                    font-size: 12px;\n                    font-weight: 500;\n                    color: #fff;\n                    text-transform: uppercase;\n                  }\n                }\n\n                .file-info {\n                  flex: 1;\n                  min-width: 0;\n                  display: flex;\n                  flex-direction: column;\n                  gap: 2px;\n\n                  .file-name {\n                    font-size: 12px;\n                    color: #fff;\n                    overflow: hidden;\n                    text-overflow: ellipsis;\n                    white-space: nowrap;\n                  }\n\n                  .file-size {\n                    font-size: 10px;\n                    color: rgba(255, 255, 255, 0.8);\n                  }\n                }\n              }\n            }\n          }\n        }\n        &.user {\n          align-items: flex-end;\n          .attachments-container {\n            align-self: flex-end;\n          }\n          .chat-content {\n            background: #45c98d;\n            color: #fff;\n            .timestamp {\n              color: #fff;\n            }\n\n            .message-attachments {\n              .attachment-image {\n                .attachment-name {\n                  color: rgba(255, 255, 255, 0.8);\n                }\n              }\n\n              .attachment-file {\n                a {\n                  background: rgba(0, 0, 0, 0.1);\n                  color: #fff;\n\n                  &:hover {\n                    background: rgba(0, 0, 0, 0.2);\n                  }\n                }\n              }\n            }\n          }\n        }\n        img {\n          max-width: 100%;\n          width: 100%;\n          margin: 12px auto;\n          display: block;\n        }\n      }\n    }\n    .chat-footer {\n      display: flex;\n      flex: 0 0 auto;\n      padding: 1rem;\n      justify-content: space-between;\n      border-top: 1px solid #ddd;\n      .chat-input {\n        flex: 1 1 auto;\n        display: flex;\n        flex-direction: column;\n        border-radius: 0.5rem;\n        background-color: transparent;\n        padding: 0.5rem;\n        border: 1px solid #ddd;\n        position: relative;\n        max-width: 100%;\n\n        .attachments-preview {\n          margin-bottom: 8px;\n          width: 100%;\n          display: flex;\n          flex-wrap: wrap;\n          gap: 8px;\n\n          .attachment-item {\n            display: flex;\n            align-items: center;\n            gap: 8px;\n            padding: 8px;\n            background: #f5f5f5;\n            border-radius: 8px;\n            margin-bottom: 4px;\n            max-width: 200px;\n            position: relative;\n\n            .attachment-preview {\n              width: 40px;\n              height: 40px;\n              border-radius: 4px;\n              overflow: hidden;\n              display: flex;\n              align-items: center;\n              justify-content: center;\n              background: #fff;\n              border: 1px solid #ddd;\n\n              img {\n                width: 100%;\n                height: 100%;\n                object-fit: cover;\n              }\n\n              .file-extension {\n                font-size: 12px;\n                font-weight: 500;\n                color: #666;\n                text-transform: uppercase;\n              }\n            }\n\n            .attachment-info {\n              flex: 1;\n              min-width: 0;\n              display: flex;\n              flex-direction: column;\n              gap: 2px;\n\n              .attachment-name {\n                font-size: 12px;\n                color: #333;\n                overflow: hidden;\n                text-overflow: ellipsis;\n                white-space: nowrap;\n              }\n\n              .attachment-size {\n                font-size: 10px;\n                color: #666;\n              }\n            }\n\n            .upload-progress {\n              position: absolute;\n              top: 0;\n              left: 0;\n              right: 0;\n              height: 2px;\n              background: rgba(69, 201, 157, 0.2);\n              overflow: hidden;\n\n              &::after {\n                content: '';\n                position: absolute;\n                top: 0;\n                left: 0;\n                height: 100%;\n                width: 100%;\n                background: #45c98d;\n           